nav {
    margin: 0 auto;
    max-width: 650px;
    line-height: 1.6;
    font-size: 14px;
    color: #444;
    padding: 0 10px;
    /*   IE11 workaround   */
    display: block;
    text-align: right;
}

nav > ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

nav > ul > li {
    display: inline;
}

nav > ul > li + li::before {
    content: " | ";
}

nav > ul abbr {
    color: #444;
    text-decoration: none;
}

main {
    margin: 0 auto;
    max-width: 650px;
    line-height: 1.6;
    font-size: 18px;
    color: #444;
    padding: 0 10px;
    /*   IE11    */
    display: block;
}

.highlight {
    background-color: yellow;
}

section {
    page-break-inside: avoid;
}

h1, h2, h3 {
    line-height: 1.2;
}

h2 {
    font-size: 1.35em;
}

h3 {
    font-size: 18px;
}

a {
    color: #444;
    text-decoration: underline;
}

h2 > a {
    text-decoration: none;
}

h2 > a:focus {
    color: #444;
    text-decoration: underline;
}

h2 > a:visited {
    color: #444;
    text-decoration: underline;
}

h2 > a:active {
    color: #444;
    text-decoration: underline;
}

a:hover {
    color: #777;
    text-decoration: underline;
}

a:focus {
    color: #777;
    text-decoration: underline;
}

a:visited {
    color: #777;
    text-decoration: underline;
}

a:active {
    color: #777;
    text-decoration: underline;
}

section#skills > ul {
    columns: 316px;
    padding: 0;
}

section#skills > ul > li {
    break-inside: avoid;
    -webkit-column-break-inside: avoid;
    padding-bottom: 10px;
    margin-left: 40px;
}

section#skills > ul > li > ul {
    margin-top: 0;
}

section#skills > ul > li > h3 {
    margin: 0;
}

hr {
    clear: both;
}

@media print {
    a {
        text-decoration: none;
    }

    a:hover {
        color: #444;
        text-decoration: none;
    }

    a:focus {
        text-decoration: none;
    }

    a:visited {
        text-decoration: none;
    }

    a:active {
        text-decoration: none;
    }

    a:link {
        text-decoration: none;
    }
}